/* BREAKPOINT */
.text-clamp, .text-clamp--1, .text-clamp--2, .product__title, .single-product__title, .single-product__revproduct-title, .widget-product__title {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.post__image {
  min-height: 100%;
  width: auto;
}

.product__price bdi, .single-product__price bdi, .single-product__revproduct-price bdi, .widget-product__price bdi {
  font-size: 1.6rem;
  color: #ff7039;
  margin-left: 0.6rem;
  text-decoration: none;
}

.product__price > del bdi, .single-product__price > del bdi, .single-product__revproduct-price > del bdi, .widget-product__price > del bdi {
  font-size: 1.4rem;
  color: #666;
  text-decoration: line-through;
}

.product__price > ins, .single-product__price > ins, .single-product__revproduct-price > ins, .widget-product__price > ins {
  text-decoration: none;
}

.product__price > ins bdi, .single-product__price > ins bdi, .single-product__revproduct-price > ins bdi, .widget-product__price > ins bdi {
  font-size: 1.6rem;
  color: #ff7039;
  margin-left: 0.6rem;
}

.home-popularCate__header h3, .home-moreproducts__header h3, .single-product__reviews-head, .related-products__head {
  font-size: 2rem;
  color: rgba(0, 0, 0, 0.87);
  line-height: 1.5;
  font-weight: 400;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-transform: uppercase;
}

.home-promotion__body .owl-nav, .related-products__list .owl-nav {
  display: block;
}

.home-promotion__body .owl-nav .owl-prev, .related-products__list .owl-nav .owl-prev,
.home-promotion__body .owl-nav .owl-next,
.related-products__list .owl-nav .owl-next {
  top: 45%;
  outline: 0;
  background-color: #ffffff !important;
  display: block;
  font-size: 1rem !important;
  color: #e3e3e3 !important;
  height: 3.8rem;
  width: 3.8rem;
  line-height: 2.8rem !important;
  position: absolute;
  text-align: center;
  opacity: 0;
  border: 0.1rem solid #e2e2e2 !important;
  border-radius: 50%;
  z-index: 100;
  transform: scale(1);
  transform-origin: center;
  transform-style: flat;
  transition-property: [opacity width height];
  transition-duration: 0.2s;
  transition-timing-function: ease;
  transition-delay: 0s;
}

.home-promotion__body .owl-nav .owl-prev, .related-products__list .owl-nav .owl-prev {
  left: -1.5rem;
  right: auto;
}


.home-promotion__body .owl-nav .owl-next,
.related-products__list .owl-nav .owl-next {
  right: -1.5rem;
  left: auto;
}

.home-promotion__body:hover .owl-nav .owl-prev, .related-products__list:hover .owl-nav .owl-prev,
.home-promotion__body:hover .owl-nav .owl-next,
.related-products__list:hover .owl-nav .owl-next {
  opacity: 1;
  transform: scale(1.2);
}

@media (max-width: 519px) {
  
  .home-promotion__body .owl-nav .owl-next,
  .related-products__list .owl-nav .owl-next {
    right: 0.5rem;
    opacity: 1;
    transform: scale(1.2);
  }
  .home-promotion__body .owl-nav .owl-prev, .related-products__list .owl-nav .owl-prev {
    left: 0.5rem;
    opacity: 1;
    transform: scale(1.2);
  }
}

.woocommerce-Input.woocommerce-Input--text.input-text, .woocommerce-input-wrapper > input, .woocommerce-Input.input-text, .input-text {
  border: 0.1rem solid #dedede;
  border-radius: 0;
  padding: 0.5rem 1rem;
  background: transparent;
  transition-property: all;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  transition-delay: 0s;
  height: 3.5rem;
  font-size: 1.4rem;
}

.woocommerce-input-wrapper > input:focus, .input-text:focus {
  border: 0.1rem solid #999;
}

